// @ts-nocheck
import React, { useEffect, useState, useContext } from 'react';
import { Card, Button, TextField, Avatar } from "@material-ui/core";
import { FirebaseContext } from "../firebaseContext";

export const Upvote = () => {
  const [actives, setActives] = useState();
  const [interests, setInterests] = useState();
  const { getAllProfiles, getActives, setUpvote, getUpvote } = useContext(FirebaseContext);
  const [input, setInput] = useState();
  const [currWallet, setCurrWallet] = useState("tz28VHDkPm9do7KyjARgW2nFFQ9DjfbaWR9x");
  const [toggleActive, setToggleActive] = useState(false);
  const [currUpvote, setCurrUpvote] = useState(0);

  useEffect(() => {

    async function grabActives() {
      let res = await getActives();
      console.log(res);
      setActives(res);
    }

    async function getProfiles() {
      let res = await getAllProfiles();
      console.log(res);
      setInterests(res);
    }

    if (toggleActive) {
      grabActives();
    } else {
      getProfiles();
    }

  }, [toggleActive, getActives, getAllProfiles]);


  useEffect(() => {
    async function grabUpvote() {
      let res = (await getUpvote(currWallet));

      setCurrUpvote(res.amount)
    }
    grabUpvote()
  }, [getUpvote, currWallet]);
  /*
  useEffect(() => {
    if (toggleActive) {

    }
  }, [toggleActive]);

    useEffect(() => {
      const interval = setInterval(async () => {
        if (toggleActive) {
          let res = await getActives();
          console.log(res);
          setActives(res);
        }
      }, 120000);
  
      return () => clearInterval(interval); // This represents the unmount function, in which you need to clear your interval to prevent memory leaks.
    }, [toggleActive])
  */
  const handleChangeInput = (event: React.ChangeEvent<HTMLInputElement>) => {
    setInput(event.target.value)
  };

  const handleKeyPress = (event) => {
    if (event.key === "Enter") {
      window.open(`https://assemblr.xyz/?profile=${input}`, "_blank")
    }
  };

  return (
    <Card style={{ height: "100vh", overflow: "auto", fontFamily: "roboto" }}>
      <h1 style={{ textAlign: "center", fontSize: "3em" }}>UPVOTE</h1>

      <div style={{ display: "flex", justifyContent: "center", paddingBottom: 20 }}>
        {false && <Card style={{ border: "solid 1px black", padding: 20, display: "flex", alignItems: "center" }}>
          <TextField id="outlined-basic" variant="outlined"

            onChange={handleChangeInput}
            value={input}
            onKeyPress={handleKeyPress}
            label="enter wallet address or .tez"
          />

          <Button
            variant="contained" color="primary"
            style={{ marginLeft: 20 }}
            onClick={() => {
              window.open(`https://assemblr.xyz/?profile=${input}`, "_blank")
            }}
          >
            VISIT
          </Button>

        </Card>
        }
      </div>

      <div style={{ display: "flex", width: "99%", marginLeft: 10 }}>
        <div style={{ width: "30%" }}>
          <div style={{ display: "flex", justifyContent: "center" }}>
            <Button
              size={"large"}
              title={"active"}
              onClick={() => {
                setToggleActive(true)
              }}
              style={{
                backgroundColor: toggleActive ? "black" : "white", width: "100%", color: toggleActive ? "white" : "black"
              }}
            >
              Active
            </Button>
            <Button
              size={"large"}
              title={"active"}
              onClick={() => {
                setToggleActive(false)
              }}
              style={{
                backgroundColor: !toggleActive ? "black" : "white", width: "100%", color: !toggleActive ? "white" : "black"
              }}
            >
              Interesting
            </Button>
          </div>
          <div style={{ maxHeight: "80vh", overflow: "scroll", border: "solid 1px black" }}>
            {!toggleActive && interests && interests.map((profile) => (
              profile &&
              <div style={{ display: "flex", alignItems: "center" }}>
                <Button>{profile.upvotes}</Button>
                <Avatar
                  alt="Profile Picture"
                  src={`https://services.tzkt.io/v1/avatars/${profile.address}`}
                />
                <div style={{ textAlign: "center" }}>
                  <Button
                    size={"large"}
                    title={profile.address}
                    onClick={() => {
                      //window.open(`https://assemblr.xyz/?profile=${profile}`, "_blank")
                      setCurrWallet(profile.address)
                    }}
                    style={{ width: "100%", color: "black" }}
                  >
                    <u>{profile.address}</u>&nbsp;
                  </Button>
                </div>
              </div>
            ))
            }
            {toggleActive && actives && actives.map((profile) => (
              profile &&
              <div style={{ display: "flex", alignItems: "center" }}>
                <Avatar
                  alt="Profile Picture"
                  src={`https://services.tzkt.io/v1/avatars/${profile}`}
                />
                <div style={{ textAlign: "center" }}>
                  <Button
                    size={"large"}
                    title={profile}
                    onClick={() => {
                      //window.open(`https://assemblr.xyz/?profile=${profile}`, "_blank")
                      setCurrWallet(profile)
                    }}
                    style={{ width: "100%", color: "black" }}
                  >
                    <u>{profile}</u>&nbsp;
                  </Button>
                </div>
              </div>
            ))
            }
          </div>
        </div>

        <Card style={{ backgroundColor: "white", width: "100%", padding: 20, height: "80vh", border: "solid 1px black", marginLeft: 10 }}>
          <div style={{ display: "flex", alignItems: "center" }}>
            <div style={{ display: "flex", justifyContent: "left", alignItems: "center" }}>
              <b style={{ paddingRight: 10, fontSize: "2em" }} >{currUpvote}</b>
              <Button
                onClick={async () => {
                  let res = await setUpvote(currWallet);
                  if (res.isSuccessful) {
                    setCurrUpvote(currUpvote + 1)
                  }
                }}
              >
                <u>Upvote</u>
              </Button>
            </div>
            <b style={{ marginLeft: "auto" }}>{currWallet}</b>
          </div>
          <iframe title={"temp"} src={`https://assemblr.xyz/?profile=${currWallet}`} width={"100%"} height={"94%"}></iframe>

        </Card>
      </div>
    </Card >
  );
}
